x;list-style:none;text-decoration:none;background-color:#bbb;width:20%;border-top:2px solid #fff;border-bottom:2px solid #000;border-radius:50%;opacity:.7}.profile-link:hover{opacity:1}.profile-link-image{height:5vmin}#waylon{position:absolute;left:15%;width:20%;height:auto;transition-property:top,transform;transition-duration:.3s}#thar-she-blows{position:absolute;left:25%;height:10%;opacity:0;transition-property:opacity;transition-duration:.2s;transition-timing-function:ease-out}#modal{position:fixed;display:flex;align-items:center;justify-content:center;z-index:2;margin-bottom:50em;left:0;top:0;width:100%;height:100%;overflow:hidden;background-color:rgba(0,0,80,.5)}.modal-content{position:relative;background-image:linear-gradient(#5AFFFF, #46CACA);margin:auto 20%;padding:3.5vmin;box-shadow:-1px 10px 2px 1px rgba(0,0,0,.6);border-radius:50px;animation-name:zoom;animation-duration:.9s;-webkit-animation-name:zoom;-webkit-animation-duration:.9s}.story{font-family:"Gloria Hallelujah",cursive,Arial,Helvetica,sans-serif;font-size:2.2vmin;line-height:3vmin;color:#134b88;font-weight:700;text-align:center}.boom{font-size:5vmin;line-height:7vmin;text-transform:uppercase;text-align:center}.emphasis{font-weight:900;color:#000050}.instructions-list{padding-left:3.5vmin}.instructions-heading{font-family:Arial,Helvetica,sans-serif;font-size:2.1vmin;line-height:3.2vmin}.instructions-text{font-family:Arial,Helvetica,sans-serif;font-size:1.5vmin;line-height:2vmin;list-style:disc inside url("../images/bubble-bullet.png");text-indent:-3vmin}@keyframes zoom{from{transform:scale(0)}to{transform:scale(1)}}@-webkit-keyframes zoom{from{-webkit-transform:scale(0)}to{-webkit-transform:scale(1)}}